import {
    Component,
    Prop,
    Element,
    Method,
    h,
    Host,
    EventEmitter,
    Event,
} from '@stencil/core';

/**
 * @category Layout
 * @description A collapsible content container component that allows users to show/hide sections of content. Supports multiple panels, animations, and programmatic control of expanded states.
 *
 * @slot accordion-trigger - Used for placing the atui-accordion-trigger for this accordion item. Only use if not using the 'label' prop on this element.
 * @slot default - Used for placing the content for this accordion item.
 */
@Component({
    tag: 'at-accordion-item',
    shadow: false,
})
export class AtAccordionItemComponent {
    @Element() el: HTMLElement;
    /**
     * Used to identify the accordion item among the other accordion items
     */
    @Prop() item_id!: string;

    /**
     * Provides an atui-accordion-trigger with this label if set
     */
    @Prop() label: string;

    /**
     * Puts a light gray separator below the accordion item
     */
    @Prop() border: boolean = true;

    /**
     * String content to display when the accordion is open
     */
    @Prop() content: string;

    /**
     * Used for setting if the accordion is open.
     */
    @Prop({ mutable: true }) open: boolean = false;

    /**
     * Emitted when the accordion item's open state changes
     */
    @Event() atuiAccordionChange: EventEmitter<boolean>;

    private accordionElement: HTMLDetailsElement;

    componentDidLoad() {
        this.accordionElement = this.el.querySelector('details');
    }

    private async handleSummaryInteraction(event: Event) {
        if (event.type === 'click' || event.type === 'touchend') {
            event.preventDefault();
            if (this.open) {
                await this.closeAccordion();
            } else {
                await this.openAccordion();
            }
        }
    }

    /**
     * Opens this accordion item
     */
    @Method()
    async openAccordion(): Promise<void> {
        if (this.open) return;
        this.accordionElement.open = true;
        this.open = true;
        this.atuiAccordionChange.emit(this.open);
    }

    /**
     * Closes this accordion item
     */
    @Method()
    async closeAccordion(): Promise<void> {
        if (!this.open) return;
        this.accordionElement.open = false;
        this.open = false;
        this.atuiAccordionChange.emit(this.open);
    }

    /**
     * Return the accordion items open state
     */
    @Method()
    async getIsOpen(): Promise<boolean> {
        return this.open;
    }

    render() {
        return (
            <Host
                data-name={this.item_id}
                data-state={this.open ? 'expanded' : 'collapsed'}
            >
                <details
                    class={`group/accordion-item'}`}
                    ref={(el) =>
                        (this.accordionElement = el as HTMLDetailsElement)
                    }
                    role="group"
                    open={this.open}
                >
                    <summary
                        id={`trigger-${this.item_id}`}
                        role="button"
                        aria-expanded={this.open}
                        data-state={this.open ? 'expanded' : 'collapsed'}
                        class="group/accordion-item focus-within:ring-active-foreground/30 flex rounded-sm outline-0 focus-within:ring-2"
                        onClick={(event) =>
                            this.handleSummaryInteraction(event)
                        }
                        onTouchEnd={(event) =>
                            this.handleSummaryInteraction(event)
                        }
                        aria-controls={`content-${this.item_id}`}
                    >
                        {this.label && (
                            <at-accordion-trigger
                                label={this.label}
                            ></at-accordion-trigger>
                        )}
                        <slot name="accordion-trigger" />
                    </summary>
                    <div
                        id={`content-${this.item_id}`}
                        aria-labelledby={`trigger-${this.item_id}`}
                        hidden={!this.open}
                        data-name="accordion-item-content"
                    >
                        {this.content && (
                            <div class="flex flex-col p-16 leading-normal">
                                {this.content}
                            </div>
                        )}
                        <slot />
                    </div>
                </details>
            </Host>
        );
    }
}
